import React, { Component } from 'react'
import CountUp from 'react-countup';

class WeAreProud extends Component {

    render() {
        return (
            <div id='weAreProud'>
                <div className='container text-center'>
                    <h1>WE ARE PROUD OF THESE NUMBERS</h1>
                    <p>Curabitur non nulla sit amet nisl tempus convallis quis ac lectus.
                Vivamus suscipit tortor eget felis porttitor volutpat.
                Curabitur aliquet quam id dui posuere.
                </p>
                    <div className="col-md-3" id='numbers'>
                        <div id='numbers1'>
                        <CountUp start={0} end={1600} duration={4} useEasing={true}/>
                            <p>HAPPY CLIENTS</p>
                        </div>
                    </div>
                    <div className="col-md-3" id='numbers'>
                        <div id='numbers1'>
                        <CountUp start={0} end={3200} duration={4} useEasing={true} />
                            <p>COMPLETED PROJECTS</p>
                        </div>
                    </div>
                    <div className="col-md-3" id='numbers'>
                        <div id='numbers1'>
                        <CountUp start={0} end={40} duration={4} useEasing={true}/>  
                            <p>AWARDS</p>
                        </div>
                    </div>
                    <div className="col-md-3" id='numbers'>
                        <div id='numbers1'>
                        <CountUp start={0} end={20000} duration={4} useEasing={true}/>
                            <p>COFFE DRINKS</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default WeAreProud;


