import React from 'react'
import Scroll from 'react-scroll'
const Go = Scroll.Link;

const DoYouLike = () => {
    return (
        <div className='container text-center' id='doyoulike'>
            <Go to="head" spy={true} smooth={true} offset={-100} duration={1000}><button id='hb'></button></Go>
            <h1>DO YOU LIKE OUR PORTFOLIO?</h1>
            <p>Curabitur non nulla sit amet nisl tempus convallis quis ac lectus.
                Vivamus suscipit tortor eget felis porttitor volutpat.
                Curabitur aliquet quam id dui posuere.
                </p>
            <button id='lb'>CONTACT US</button>
        </div>
    )
}
export default DoYouLike;


