import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Scroll from 'react-scroll'
const Element = Scroll.Element;

class OurWorks extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hello: 1
        }
    }
    render() {
        return (
            <div id='ourworks'>
                <div className='container text-center'>
                    <Element name='ourSerivces' className='element'>
                        <h4 id='ourSerivces'>OUR WORKS</h4>
                    </Element>
                    <h2 id='whatWeDo'>WHAT WE DONE SO FAR</h2>
                    <hr id='hr' />
                    <h5 id='servText'>Curabitur non nulla sit amet nisl tempus convallis quis ac lectus.
                Vivamus suscipit tortor eget felis porttitor volutpat.
                Curabitur aliquet quam id dui posuere blandit. Nulla porttitor
                accumsan tincidunt. Cras ultricies ligula sed magna dictum porta.
                Curabitur non nulla sit amet nisl tempus convallis quis ac lectus.
                Praesent sapien massa, convallis a pellen
                tesque nec, egestas non nisi.
                Mauris blandit aliquet elit,
                eget tincidunt ni dictum porta.</h5>
                    <div>
                        <ul id='allworks'>
                            <li id='alllink'><Link to='all'>All</Link></li>
                            <li><Link to='webdesign'>WEB DESIGN</Link></li>
                            <li><Link to='graphdesign'>GRAPHIC DESIGN</Link></li>
                            <li><Link to='photography'>PHOTOGRAPHY</Link></li>
                            <li><Link to='illustration'>ILLUSTRATION</Link></li>
                        </ul>
                    </div>
                </div>
                <div className='col-md-12'>
                    <div className="col-md-2"><img src="" alt="" className="img-responsive" /></div>
                    <div className="col-md-2"><img src="" alt="" className="img-responsive" /></div>
                    <div className="col-md-2"><img src="" alt="" className="img-responsive" /></div>
                    <div className="col-md-2"><img src="" alt="" className="img-responsive" /></div>
                    <div className="col-md-2"><img src="" alt="" className="img-responsive" /></div>
                    <div className="col-md-2"><img src="" alt="" className="img-responsive" /></div>
                </div>
            </div>
        );
    }
}

export default OurWorks;


