import React from 'react'
import Scroll from 'react-scroll'
const Go = Scroll.Link;    

const Footer = () => {
    return (
        <div id='footer'>
            <div className="container text-center">
                <h2> Copyright 2017 <span>Smak</span> . All Rights Reserved.</h2>
                <div id='icons'>
                    <a href='https://www.facebook.com/'> <i className="fa fa-facebook" aria-hidden="true"></i></a>
                    <a href='https://dribbble.com/'> <i className="fa fa-dribbble" aria-hidden="true"></i></a>
                    <a href='https://twitter.com/'> <i className="fa fa-twitter" aria-hidden="true"></i></a>
                    <a href='https://google.com'> <i className="fa fa-google-plus" aria-hidden="true"></i></a>
                </div>
                <Go to="head" spy={true} smooth={true} offset={-100} duration={1000}><button></button></Go>
            </div>
        </div>
    )
}                               

export default Footer;


