import React, { Component } from 'react';
import logo from '../../assets/img/logos.png';
import arrleft from '../../assets/img/arrleft.png';
import arrright from '../../assets/img/arrright.png';

import Scroll from 'react-scroll'
const Element = Scroll.Element;
const Go = Scroll.Link;

class Header extends Component {
    state = {}
    render() {
        return (
            <Element name='head' className='element'>
                <div id='header'>
                    <nav className="navbar navbar-default navbar-fixed-top">
                        <div className="container">
                            <div className="navbar-header" id='navbarheader'>
                                <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
                                    <span className="sr-only">Toggle navigation</span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                </button>
                                <Go to="head" id='logo' className="navbar-brand" spy={true} smooth={true} offset={-100} duration={1000}><img src={logo} alt="" /></Go>
                            </div>
                            <div id="navbar" className="navbar-collapse collapse">
                                <ul className="nav navbar-nav navbar-right" id='all'>

                                    <li id='homelink'><Go to="head" spy={true} smooth={true} offset={-100} duration={2000}>HOME</Go></li>
                                    <li><Go to="services" spy={true} smooth={true} offset={-100} duration={2000}>SERVICES</Go></li>
                                    <li><Go to="ourSerivces" spy={true} smooth={true} offset={-100} duration={2000}>WORK</Go></li>
                                    <li><Go to="about" spy={true} smooth={true} offset={-100} duration={2000}>ABOUT</Go></li>
                                    <li><Go to="services" spy={true} smooth={true} offset={-100} duration={2000}>CLIENTS</Go></li>
                                    <li><Go to="services" spy={true} smooth={true} offset={-100} duration={1000}>CONTACT</Go></li>
                                </ul>
                            </div>
                        </div>
                    </nav>
                    <div id="myCarousel">
                        <div className="container">
                            <div id='start' className="carousel slide" data-ride="carousel">
                                <div className="carousel-inner" role="listbox">
                                    <div className="item">
                                        <div className="container text-center">
                                                <h1>BRANDING HAS ANOTHER DEFINITION</h1>
                                                <Go to="services" spy={true} smooth={true} offset={-100} duration={1000}><button>BUY THIS THEME</button></Go>
                                        </div>
                                    </div>
                                    <div className="item active">
                                        <div className="container text-center">
                                            <h1>BRANDING HAS ANOTHER DEFINITION</h1>
                                            <Go to="services" spy={true} smooth={true} offset={-100} duration={1000}><button>BUY THIS THEME</button></Go>
                                        </div>
                                    </div>
                                    <div className="item">
                                        <div className="container text-center">
                                            <h1>BRANDING HAS ANOTHER DEFINITION</h1>
                                            <Go to="services" spy={true} smooth={true} offset={-100} duration={1000}><button>BUY THIS THEME</button></Go>
                                        </div>
                                    </div>
                                </div>
                                <a className="left carousel-control" href="#start" role="button" data-slide="prev">
                                    <img src={arrleft} alt=""/>
                                </a>
                                <a className="right carousel-control" href="#start" role="button" data-slide="next">
                                <img src={arrright} alt=""/>                                
                                </a>
                            </div>
                        </div>
                    </div>
                </div>

            </Element>
        );
    }
}

export default Header;


